:root {
  --app-bg: #f2f4f8;
  --sidebar-bg: rgb(223, 232, 240);
  --primary-color: #297bf6;
  --primary-color-alt: #175dc5;
  --highlight-color: #393848;
  --default-font-color: #393848;
  --alternative-font-color: #84899f;
  --default-border-color: #9f9ea0;
  --red-color: #df2d2d;
}

h4 {
  font-size: 0.8rem;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  height: 100%;
}

body {
  font-size: 16px;
  color: #393848;
  color: var(--default-font-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: var(--app-bg);
  height: 100%;
  min-height: 100%;
  
}
.main-page {
  min-height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.top {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

/* Large screens ----------- */
@media only screen and (min-width: 768px) {
  .top {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
}

.search {
  padding: 6px;
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.search > form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex:1 1;
          flex:1 1;
}

.search__input {
  padding: 8px;
  border: 1px solid var(--default-border-color);
  border-radius: 0;
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
  border-right-width: 0;
  -webkit-flex:1 1;
          flex:1 1;
}
.search__input:focus {
}

.search__btn {
  border-color: var(--primary-color);
  background-color: var(--primary-color);
  padding: 8px 12px;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  color: #fff;
}
.search__btn:hover {
  background: var(--primary-color-alt);
}

/* Large screens ----------- */
@media only screen and (min-width: 768px) {
  .search{
    width: 50%;
  }
  .search__input {
    font-size: 1.2rem;
  }
  .search__btn {
    font-size: 1.2rem;
  }
}

.searchbar{
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 10px;
}
.logo{
    background-color: var(--sidebar-bg);
    font-size: 2rem;
    text-align: center;
    font-weight: bold;
    color: var(--alternative-font-color);
    padding-top:14px;
}

.fa-book{
    color: var(--red-color);
    margin-right: 8px;
}

/* Large screens ----------- */
@media only screen  and (min-width : 768px) {
    .logo {
        width: 20%;
    }
}
.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex:1 1;
          flex:1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.article {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: stretch;
          justify-content: stretch;
}

.aside {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: var(--sidebar-bg);
  margin: 0;
  -webkit-justify-content: stretch;
          justify-content: stretch;
}
/* Large screens ----------- */
@media only screen and (min-width: 768px) {
  .content {
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 0px;
  }
}

.sidebar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.sidebar__books-filters-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  max-height: 40%;
}
.sidebar__book-preview {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
}

/* Large screens ----------- */
@media only screen and (min-width: 768px) {
  .aside {
    width: 20%;
  }
}

.book-preview {
  -webkit-flex: 1 1;
          flex: 1 1;
  background: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
  padding: 8px;
  width: 100%;
}

.book-preview__year {
  position: absolute;
  left: 10px;
  top: 10px;
  padding: 6px 8px;
  border-radius: 26px;
  display: block;
  background: var(--highlight-color);
  color: #fff;
  font-size: 1rem;
}
.book-preview__preview {
  -webkit-align-self: center;
          align-self: center;
  height: 156px;
}
.book-preview__preview-link {
  position: absolute;
  right: 10px;
  top: 10px;
}
.book-preview__title {
  font-size: 1.2rem;
  margin: 16px 0 10px;
  height: 48px;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.book-preview__title > small {
  display: block;
  font-size: 0.7rem;
}
.book-preview__author {
    padding: 4px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
.book-preview__author {
  font-size: 0.7rem;
  margin: 0;
  color: var(--alternative-font-color);
}

.book-preview__description {
  margin-top: 8px;
  font-size: 0.6rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow-y: scroll;
}

.no-book-preview {
  color: var(--alternative-font-color);
  text-align: center;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.rating {
  display: block;
  font-size: 16px;
  position: relative;
  overflow: hidden;
}

.rating-wrap {
  display: inline-block;
  position: relative;
  height: 19px;
}

.rating .stars-active {
  color: #eebd01;
  position: relative;
  z-index: 10;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

.rating .stars-inactive {
  color: var(--alternative-font-color);
  position: absolute;
  top: 0;
  left: 0;
  -webkit-text-stroke: initial;
}

.sidebar__books-filters-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.sidebar__books-filters-container > h4 {
  text-align: center;
}
.sidebar__books-filters {
  list-style: none;
  margin: 0;
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow-y: scroll;
}

.sidebar__books-filters__filter {
  text-align: center;
}

.sidebar__books-filters__filter > a {
  text-decoration: none;
  color: var(--alternative-font-color);
  padding: 6px 20px;
  border-radius: 6px;
  cursor: pointer;
  display: block;
}
.sidebar__books-filters__filter > a:hover {
  background: var(--primary-color);
  border-radius: 6px;
}

.sidebar__books-filters__filter--active > a {
  background-color: var(--primary-color-alt);
  color: #fff;
  border-radius: 6px;
}

.sidebar__books-filters__filter > a:hover,
.sidebar__books-filters__filter--active > a:hover {
  color: #fff;
}

.book {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: #fff;
  margin: 10px;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 10px;
  max-height: 50%;
  overflow: hidden;
}

.book:hover {
  box-shadow: -1px 15px 21px -13px rgba(0, 0, 0, 0.33);
  -webkit-transform: translate(0, -10px);
          transform: translate(0, -10px);
}

.book__year {
  position: absolute;
  right: 10px;
  top: 10px;
  padding: 6px 8px;
  border-radius: 16px;
  display: block;
  background: var(--highlight-color);
  color: #fff;
  font-size: 0.8rem;
}
.book__preview {
  width: 128px;
  height: 157px;
  cursor: pointer;
}

.book__title {
  padding: 4px;
  font-size: 0.8rem;
  margin: 0;
  width: 120px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.book__authors {
  font-size: 0.7rem;
  padding: 4px;
  width: 120px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.book__rating,
.book__footer {
  font-size: 0.6rem;
  padding: 6px;
}

/* Large screens ----------- */
@media only screen and (min-width: 768px) {
  .book {
    width: 128px;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  .book__preview {
    border-radius: 10px 10px 0 0;
  }
}

@media screen and (min-width: 991px) {
  .book {
    max-height: 40%;
  }
  .book__preview {
    border-radius: 10px 10px 0 0;
  }
}

